// حاسبة العمولة والربح - القواعد الجديدة المبسطة
// التحويل: 0% (مجاناً)
// السحب: 10 في الألف (1%)

// حساب العمولة حسب نوع المعاملة
export function calculateCommission(amount: number, txnType: 'send' | 'receive'): number {
  if (txnType === 'send') {
    return 0; // التحويل مجاناً
  } else {
    // السحب 10 في الألف (1%)
    return Math.round(amount * 0.01 * 100) / 100; // تقريب لرقمين عشريين
  }
}

// حساب الربح (حالياً = العمولة، يمكن إضافة تكلفة التشغيل لاحقاً)
export function calculateProfit(amount: number, txnType: 'send' | 'receive', operatorCost: number = 0): number {
  const commission = calculateCommission(amount, txnType);
  const profit = Math.max(0, commission - operatorCost); // الربح لا يمكن أن يكون سالباً
  return Math.round(profit * 100) / 100; // تقريب لرقمين عشريين
}

// تنسيق المبلغ للعرض
export function formatAmount(amount: number): string {
  return Number(amount || 0).toLocaleString('en-US', {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2
  });
}
